import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import CustomTextField from '../../components/CustomTextField'
import CustomeTextArea from '../../components/CustomeTextArea'
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import camera from '../../Assets/Img/camera.jpg'
import routes from "../../routes/routes";
import ImageUpload from './ImageUpload'
import BaseUrl from '../constant'
import axios from 'axios'
import { Link } from 'react-router-dom'
import InputLabel from "@material-ui/core/InputLabel";
import Button from '@material-ui/core/Button';
import itemImage from '../../Assets/itemImage.png'
import { createNotification } from "../../components/Toast";
import { useDispatch, useSelector } from "react-redux";

console.log("BaseUrl", BaseUrl)
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '97%',
    paddingLeft: '4%',
    paddingTop: '1%',
    backgroundColor: "#F9FAFC",
  },
}));

console.log("env", process.env.BASEURL)
const CreateVendors = (props) => {
  const classes = useStyles();
  const [firstName, setFirstName] = React.useState(0);
  const [vendorName, setVendorName] = React.useState();
  const [vendorNameerror, setVendorNameError] = React.useState();
  const [vendorNameMessage, setVendorNameMessage] = React.useState();

  const [vendorDescription, setVendorDescription] = React.useState();
  const [vendorDescriptionError, setVendorDescriptionError] = React.useState();
  const [vendorDescriptionMessage, setVendorDescriptionMessage] = React.useState();

  const [contactName, setContactName] = React.useState();
  const [contactNameError, setContactNameError] = React.useState();
  const [contactNameMessage, setContactNameMessage] = React.useState();

  const [phone, setPhone] = React.useState();
  const [phoneError, setPhoneError] = React.useState();
  const [phoneMessage, setPhoneMessage] = React.useState();


  const [email, setEmail] = React.useState();
  const [emailError, setEmailError] = React.useState(false);
  const [emailMessage, setEmailMessage] = React.useState();

  const [contactFax, setContactFax] = React.useState();
  const [contactFaxError, setContactFaxError] = React.useState(false);
  const [contactFaxMessage, setContactFaxMessage] = React.useState();


  const [orderEmail, setOrderEmail] = React.useState();
  const [orderEmailError, setOrderEmailError] = React.useState(false);
  const [orderEmailMessage, setOrderEmailMessage] = React.useState();

  const [orderFax, setOrderFax] = React.useState();
  const [orderFaxError, setOrderFaxError] = React.useState(false);
  const [orderFaxMessage, setOrderFaxMessage] = React.useState();

  const [vendorAverageFullfillmentdays, setVendorAverageFullfillmentdays] = React.useState();
  const [vendorAverageFullfillmentdaysError, setVendorAverageFullfillmentdaysError] = React.useState(false);
  const [vendorAverageFullfillmentdaysMessage, setVendorAverageFullfillmentdaysMessage] = React.useState();

  const [averageShippingDaysFromVendor, setaverageShippingDaysFromVendor] = React.useState();
  const [averageShippingDaysFromVendorError, setaverageShippingDaysFromVendorError] = React.useState(false);
  const [averageShippingDaysFromVendorMessage, setaverageShippingDaysFromVendorMessage] = React.useState();

  const [notes, setNotes] = React.useState();
  const [notesError, setNotesError] = React.useState(false);
  const [notesMessage, setNotesMessage] = React.useState();

  const [address, setAddress] = React.useState();
  const [addressMessage, setAddressMessage] = React.useState();
  const [addressError, setAddressError] = React.useState(false);

  const [website, setWebsite] = React.useState();
  const [websiteError, setWebsiteError] = React.useState(false);
  const [websiteMessage, setWebsiteMessage] = React.useState();

  const [weightValue, setWeightValue] = React.useState();
  const [weightValueError, setWeightValueError] = React.useState(false);
  const [weightValueMessage, setWeightValueMessage] = React.useState();

  const [weightUnit, setWeightUnit] = React.useState();
  const [weightUnitError, setWeightUnitError] = React.useState(false);
  const [weightUnitMessage, setWeightUnitMessage] = React.useState();

  const [error, setError] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)



  const [state, setState] = React.useState({
    checkedA: true,
    checkedB: true,
  });
  const [imageState, imageSetState] = React.useState({
    file: null,
    base64: null,
    objectUrl: null
  });

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };
  async function readDataUrl(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      // async event handlers
      reader.onload = e => resolve(reader.result);
      reader.onerror = e => reject(reader.error);
      reader.readAsDataURL(file);
    });
  }
  let inputFileRef = null;

  const handleChangePhotoButton = e => {
    e.preventDefault();
    inputFileRef.click();
  };
  const handleChangePhotoFileInput = e => {
    const target = e.currentTarget;
    const file = target.files.item(0);


    const data = new FormData();
    data.append('file', file)

    // validate file as image
    if (!file.type.startsWith("image/")) {
      alert("File is not an image");
      return;
    }

    // store reference to the File object and a base64 representation of it
    readDataUrl(file).then(dataUrl => {
      imageSetState({
        ...imageState,
        file,
        base64: dataUrl,
        objectUrl: URL.createObjectURL(file)
      });
    });


    axios.post(routes.api.vendor.addVendorImage.url, data,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: "Bearer" + userToken
        }

      })
      .then(function (response) {
        console.log("response", response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const { base64, objectUrl } = imageState;
  const defaultImage = "http://via.placeholder.com/300";
  console.log("base64", base64)


  const handleSubmitVendor = () => {
    if (imageState.file === '' || imageState.file === null || imageState.file === undefined) {
      console.log("imageState")
      createNotification("error", "Please Add Image ");
      return false
    }
    if (vendorName === "" || vendorName === null || vendorName === undefined) {
      setVendorNameError(true)
      setVendorNameMessage("Please Enter Name")
      return false
    }
    if (vendorDescription === "" || vendorDescription === null || vendorDescription === undefined) {
      setVendorDescriptionError(true)
      setVendorDescriptionMessage("Please Add Description")
      return false
    }
    if (contactName === "" || contactName === null || contactName === undefined) {
      setContactNameError(true)
      setContactNameMessage("Please Enter Contact Name")
      return false
    }
    if (phone === "" || phone === null || phone === undefined) {
      setPhoneError(true)
      setPhoneMessage("Please Enter Phone Number")
      return false
    }
    if (email === "" || email === null || email === undefined) {
      setEmailError(true)
      setEmailMessage("Please Enter Email Address")
      return false
    }

    if (contactFax === "" || contactFax === null || contactFax === undefined) {
      setContactFaxError(true)
      setContactFaxMessage("Please Enter Fax Number")
      return false
    }

    if (orderEmail === "" || orderEmail === null || orderEmail === undefined) {
      setOrderEmailError(true)
      setOrderEmailMessage("Please Enter Order Email address")
      return false
    }
    if (orderFax === "" || orderFax === null || orderFax === undefined) {
      setOrderFaxError(true)
      setOrderFaxMessage('Please Enter Order Fax number')
      return false
    }
    if (vendorAverageFullfillmentdays === "" || vendorAverageFullfillmentdays === null || vendorAverageFullfillmentdays === undefined) {
      setVendorAverageFullfillmentdaysError(true)
      setVendorAverageFullfillmentdaysMessage('please Enter Vendor Averege Fullfilment days')
      return false

    }
    if (averageShippingDaysFromVendor === "" || averageShippingDaysFromVendor === null || averageShippingDaysFromVendor === undefined) {
      setaverageShippingDaysFromVendorError(true)
      setaverageShippingDaysFromVendorMessage("Please Enter Averege Shipping Days from Vendor Message")
      return false
    }
    if (address === "" || address === null || address === undefined) {
      setAddressError(true)
      setAddressMessage("Please Enter Address")
      return false
    }
    if (website === "" || website === null || website === undefined) {
      setWebsiteError(true)
      setWebsiteMessage('please enter Website url')
      return false 
    } 

    if (weightValue === "" || weightValue === null || weightValue === undefined) {
      setWeightValueError(true)
      setWeightValueMessage("Please Enter Weight Value")
      return false
    }
    setLoading(true)
    axios.post(BaseUrl + 'createVendor', {
      vendorName,
      vendorAverageFullfillmentdays,
      vendorDescription,
      contactName,
      phone,
      email,
      contactFax,
      orderEmail,
      orderFax,
      averageShippingDaysFromVendor,
      address,
      website,
      vengorImage: imageState.file.name
    },
      { headers: { Authorization: "Bearer " + userToken } })
      .then(function (response) {
        console.log(response);
        setLoading(false)
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  return (
    <div className={classes.root}>
      <header className="headerCreateOrder">
        <div className="header__row">
          <div className="Texts_Display">
            Create new vendor
                </div>
        </div>
      </header>
      <Link to="/dashbord/vendors">Vendor</Link>
      <div className="row">
        <div className="col-lg-8">
          <section className="leftColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Vendor details
                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper">
                <div className="flex-column w-full">
                  <div className="pt-3 inputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                      Name
          </InputLabel>
                    <CustomTextField
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      placeholder="Give your vendor a name"
                      dummyClass="w-90"
                      error={vendorNameerror}
                      message={vendorNameMessage}
                      onChange={(e) => {
                        setVendorName(e.target.value);
                        setVendorNameError(false);
                      }}
                    />
                  </div>
                  <div className="pt-3 inputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                      Description

                 </InputLabel>
                    <CustomeTextArea
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      className="w-90 Text-Area-Form"
                      error={vendorDescriptionError}
                      message={vendorDescriptionMessage}
                      onChange={(e) => {
                        setVendorDescription(e.target.value);
                        setVendorDescriptionError(false);
                      }}
                    />
                  </div>

                </div>
              </div>
            </section>
          </section>
        </div>
        <div className="col-lg-4">
          <section className="rightColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Vendor Image
                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper h-50">
                <div className="flex-column w-full ">
                  <div className=" inputBlock">
                    <div className="grid-container">
                      <div className="grid-x grid-padding-x grid-padding-y">
                        <div className="cell">
                          <input
                            onChange={handleChangePhotoFileInput}
                            ref={input => (inputFileRef = input)}
                            style={{ display: "none" }}
                            type="file"
                          />
                          <button onClick={handleChangePhotoButton} className="button" style={{ width: '100%', height: '50vh' }}>

                            {!base64 ? (
                              <img
                                src={camera}
                                // ref={img => (this.imgRef = img)}
                                style={{ width: '100%', height: '50vh' }}
                              />
                            ) : (<img
                              src={base64}
                              style={{ width: '100%', height: '50vh' }}
                            />

                            )}
                          </button>
                        </div>
                      </div>
                      <div className="grid-x grid-padding-x grid-padding-y">
                        <div className="cell small-6">
                          <div className="callout">
                            <div className="card">
                              {/* <img
                  src={base64 || defaultImage}
                  ref={img => (this.imgRef = img)}
                  style={{width:'100%' , height:'50vh'}}
                /> */}
                            </div>
                          </div>
                        </div>


                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-8">
          <section className="leftColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Vendor General Detail

                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper">
                <div className="flex-column w-full">
                  <div className="form_input_block" >
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Contact Name
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        placeholder="Contact Name"
                        dummyClass="w-90"
                        error={contactNameError}
                        message={contactNameMessage}
                        onChange={(e) => {
                          setContactName(e.target.value);
                          setContactNameError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Phone
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        dummyClass="w-90"
                        autoComplete={false}
                        type="text"
                        placeholder="Phone"
                        error={phoneError}
                        message={phoneMessage}
                        onChange={(e) => {
                          setPhone(e.target.value);
                          setPhoneError(false);
                        }}
                      />
                    </div>
                  </div>
                  <div className="form_input_block" >
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Contact Email
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        placeholder="Contact Email"
                        dummyClass="w-90"
                        error={emailError}
                        message={emailMessage}
                        onChange={(e) => {
                          setEmail(e.target.value);
                          setEmailError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Contact Fax
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        dummyClass="w-90"
                        autoComplete={false}
                        type="text"
                        placeholder="Contact Fax"
                        error={contactFaxError}
                        message={contactFaxMessage}
                        onChange={(e) => {
                          setContactFax(e.target.value);
                          setContactFaxError(false);
                        }}
                      />
                    </div>
                  </div>
                  <div className="form_input_block" >
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Order Email
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        placeholder="Order Email "
                        dummyClass="w-90"
                        error={orderEmailError}
                        message={orderEmailMessage}
                        onChange={(e) => {
                          setOrderEmail(e.target.value);
                          setOrderEmailError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Order fax
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        dummyClass="w-90"
                        autoComplete={false}
                        type="text"
                        placeholder="Order fax"
                        error={orderFaxError}
                        message={orderFaxMessage}
                        onChange={(e) => {
                          setOrderFax(e.target.value);
                          setOrderFaxError(false);
                        }}
                      />
                    </div>
                  </div>
                  <div className="form_input_block" >
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Vendor Average Fullfillment days
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        placeholder="Optional"
                        dummyClass="w-90"
                        error={vendorAverageFullfillmentdaysError}
                        message={vendorAverageFullfillmentdaysMessage}
                        onChange={(e) => {
                          setVendorAverageFullfillmentdays(e.target.value);
                          setVendorAverageFullfillmentdaysError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        AVERAGE SHIPPING DAYS FROM VENDOR
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        dummyClass="w-90"
                        autoComplete={false}
                        type="text"
                        placeholder="Optional"
                        error={averageShippingDaysFromVendorError}
                        message={averageShippingDaysFromVendorMessage}
                        onChange={(e) => {
                          setaverageShippingDaysFromVendor(e.target.value);
                          setaverageShippingDaysFromVendorError(false);
                        }}
                      />
                    </div>
                  </div>

                </div>
              </div>
            </section>
          </section>
        </div>
        <div className="col-lg-4 h-30">
          <section className="rightColumn ">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Notes

                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper">
                <div className="flex-column w-full">
                  <div className="pt-3 inputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                      Optional
                      </InputLabel>
                    <CustomeTextArea
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      className="w-85 Text-Area-Form"
                      error={notesError}
                      message={notesMessage}
                      onChange={(e) => {
                        setNotes(e.target.value);
                        setNotesError(false);
                      }}
                    />
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
      </div>
      <div className="row mt-3">
        <div className="col-lg-8">
          <section className="leftColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Addresss Information
                </div>
                </div>
                <div className="dEgCwM">
                </div>
              </div>
              <div className="bLLHWD">
                <div className="row">
                  <div className="co-lg-12 pl-5">
                    <div className="hPSGav mt-3">

                    </div>
                  </div>
                </div>


                <div className="form_input_block" >
                  <div className="pt-3 twoinputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                      ADDRESS

                      </InputLabel>
                    <CustomeTextArea
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      className="w-95 Text-Area-Form"
                      error={addressError}
                      message={addressMessage}
                      onChange={(e) => {
                        setAddress(e.target.value);
                        setAddressError(false);
                      }}
                    />
                  </div>
                </div>
                <div className="form_input_block" >
                  <div className="pt-3 twoinputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                      Website
                      </InputLabel>
                    <CustomTextField
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      placeholder="Unit"
                      dummyClass="w-95"
                      error={websiteError}
                      message={websiteMessage}
                      onChange={(e) => {
                        setWebsite(e.target.value);
                        setWebsiteError(false);
                      }}
                    />
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
        <div className="col-lg-4">
          <section className="rightColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Weight
                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper">
                <div className="flex-column w-full">
                  <div className="pt-3 inputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                      Weight value
                      </InputLabel>
                    <CustomTextField
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      placeholder="Ex. MO-01"
                      dummyClass="w-85"
                      error={weightValueError}
                      message={weightValueMessage}
                      onChange={(e) => {
                        setWeightValue(e.target.value);
                        setWeightValueError(false);
                      }}
                    />
                  </div>

                  <div className=" inputBlock">
                    <div class="form-group w-85">
                      <label for="exampleFormControlSelect1">Unit</label>
                      <select class="form-control" id="exampleFormControlSelect1"
                        onChange={(e) => {
                          setWeightUnit(e.target.value);
                          setError(false);
                        }}>
                        <option className="DDOptions">1</option>
                        <option className="DDOptions">2</option>
                        <option className="DDOptions">3</option>
                        <option className="DDOptions">4</option>
                        <option className="DDOptions">5</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-6">
          <section className="leftColumn">
            <section className="card_content">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Vendors
                </div>
                </div>
                <div className="dEgCwM">
                  <Button variant="contained" color="primary">Edit</Button>
                </div>
              </div>
            </section>
          </section>
        </div>
        <div className="col-lg-6">
          <section className="leftColumn">
            <section className="card_content">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    LOCATIONS
                </div>
                </div>
                <div className="dEgCwM">
                  <FormControlLabel
                    control={
                      <Switch
                        checked={state.checkedB}
                        onChange={handleChange}
                        name="checkedB"
                        color="primary"
                      />
                    }
                  />
                </div>
              </div>
            </section>

          </section>
        </div>



      </div>
      <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', padding: '2%' }}>

        <Button variant="contained" color="primary" onClick={handleSubmitVendor}>
          {loading ? <div className="loader"></div> : 'Save Vendor'}

        </Button>
      </div>







    </div>
  );
}
export default CreateVendors;